.btn { display: inline-block; width: 300px; height: 60px; line-height: 60px; font-family: "OpenSans-Extrabold", Arial, sans-serif; font-size: 12px; color: #ffffff; text-align: center; text-transform: uppercase; transition: all 0.75s ease-out; }

.btn .btn__icon { display: inline-block; vertical-align: middle; height: 13px; width: 3px; position: relative; left: 12px; top: -1px; transition: all 0.75s ease-out; transform: translateZ(0); }

.btn .btn__icon:before, .btn .btn__icon:after { content: ''; position: absolute; display: block; right: 0; width: 3px; height: 7px; background-color: #ffffff; transition: all 0.75s ease-out; transform: translateZ(0); }

.btn .btn__icon:before { bottom: 50%; }

.btn .btn__icon:after { top: 50%; }

.btn .btn__icon_red:before, .btn .btn__icon_red:after { background-color: #a70a3f; }

.btn:hover .btn__icon:before { transform: rotate(-40deg) translateY(2px) scaleY(1.15) translateZ(0); }

.btn:hover .btn__icon:after { transform: rotate(40deg) translateY(-2px) scaleY(1.15) translateZ(0); }

.btn_red { background-color: #a70a3f; }

.btn_black { background-color: #000000; }

.btn_white { color: #a70a3f; background-color: #ffffff; }

.arrow-bot-anim { display: inline-block; vertical-align: middle; height: 13px; width: 3px; position: relative; transform: rotate(90deg) translateZ(0); transition: all 0.75s ease-out; }

.arrow-bot-anim:before, .arrow-bot-anim:after { content: ''; position: absolute; display: block; right: 0; width: 3px; height: 7px; background-color: #000; transition: all 0.75s ease-out; trnasform: translateZ(0); }

.arrow-bot-anim:before { bottom: 50%; }

.arrow-bot-anim:after { top: 50%; }

.arrow-bot-anim.is-active:before { transform: rotate(-40deg) translateY(2px) scaleY(1.15) translateZ(0); }

.arrow-bot-anim.is-active:after { transform: rotate(40deg) translateY(-2px) scaleY(1.15) translateZ(0); }

/* =================================== Main Styles @mixin ================================== */
.main { padding-top: 38px; }

body { position: relative; min-width: 320px; font-family: "PTSans-Regular", Arial, sans-serif; font-size: 14px; color: #000; line-height: 1.2; -webkit-font-smoothing: antialiased; background-color: #ffffff; }

.wrapper { position: relative; overflow: hidden; margin: 0 auto; min-height: 100%; }

.main { position: relative; }

.container { margin-left: auto; margin-right: auto; padding: 0 40px; max-width: 1280px; }

.preloader { position: fixed; top: 0; left: 0; right: 0; bottom: 0; z-index: 1000; background-color: #ffffff; transition: all 0.75s ease-in-out; }

.is-loaded .preloader { opacity: 0; pointer-events: none; }

/* Helpers ----------------------------------- */
button { background-color: transparent; }

a:hover, a:focus { text-decoration: none; }

a { color: inherit; text-decoration: none; }

table { vertical-align: top; }

img { max-width: 100%; vertical-align: top; }

.icon { display: inline-block; vertical-align: middle; }

.text-left { text-align: left; }

.text-center { text-align: center; }

.text-right { text-align: right; }

.is-hidden { display: none; }

.vertical { display: inline-block; vertical-align: middle; }

.cfx:after { content: ""; display: table; clear: both; }

* ::selection { color: #fff; background-color: #a70a3f; }

* ::-moz-selection { color: #fff; background-color: #a70a3f; }

/* =================================== Header @mixin ================================== */
.header { position: absolute; top: 0; left: 0; right: 0; z-index: 100; background-color: #f5558b; opacity: 0; transform: translateY(-40px); transition: all 1s ease-in-out; }

.is-loaded .header { opacity: 1; transform: translateY(0); }

.header__top { font-family: "OpenSans-Extrabold", Arial, sans-serif; color: #ffffff; line-height: 38px; background-color: #000000; opacity: 0; transform: translateY(-40px); transition: all 1s ease-in-out; }

.header__top:after { content: ""; display: table; clear: both; }

.is-loaded .header__top { opacity: 1; transform: translateY(0); }

.header__phone { float: left; position: relative; padding-left: 57px; }

.header__phone:before { content: ''; position: absolute; z-index: 1; top: 50%; left: 0; width: 30px; height: 1px; background-color: rgba(255, 255, 255, 0.2); }

.header__login { float: right; font-size: 12px; color: rgba(255, 255, 255, 0.4); text-transform: uppercase; transition: all 0.5s ease-in-out; }

.header__login:hover { color: #a70a3f; }

.header__lang { position: relative; position: relative; z-index: 101; float: right; margin-right: 30px; font-size: 12px; text-transform: uppercase; }

.header__lang-link { display: block; position: relative; padding-left: 10px; padding-right: 30px; color: #808080; line-height: 30px; background-color: #ffffff; transition: all 0.5s ease-in-out; }

.header__lang-link:before { content: ''; position: absolute; z-index: 1; top: 50%; right: 9px; width: 0; height: 3px; margin-top: -2px; background-color: transparent; transition: all 0.5s ease-in-out; }

.header__lang_parent > .header__lang-item > .header__lang-link:before, .header__lang_parent > .header__lang-item > .header__lang-link:after { content: ''; position: absolute; display: block; top: 50%; right: 9px; width: 7px; height: 3px; background-color: #4C4C4C; transition: all 0.75s ease-in-out; }

.header__lang_parent > .header__lang-item > .header__lang-link:after { right: 15px; margin-top: -2px; }

.header__lang_parent.is-active > .header__lang-item > .header__lang-link:before, .header__lang_parent.is-active > .header__lang-item > .header__lang-link:after { background-color: #c9c9c9; }

.header__lang_parent.is-active > .header__lang-item > .header__lang-link:before { margin-top: -3px; transform: rotate(-40deg) translateX(-2px) scaleY(1.15); }

.header__lang_parent.is-active > .header__lang-item > .header__lang-link:after { margin-top: -3px; transform: rotate(40deg) translateX(2px) scaleY(1.15); }

.header__lang:not(.header__lang_parent) .header__lang-item:hover > .header__lang-link { color: #a70a3f; }

.header__lang:not(.header__lang_parent) .header__lang-item:hover > .header__lang-link:before { width: 13px; background-color: #a70a3f; }

.header__lang-item.active > .header__lang-link { color: #ffffff; line-height: 38px; }

.header__lang-item.active > .header__lang-link { background-color: transparent; }

.header__lang.is-active > .header__lang-item > .header__lang-link { color: #a70a3f; background-color: #ffffff; }

.header__lang .header__lang { display: none; position: absolute; z-index: 1; top: 100%; }

.header__bot { padding: 14px 0; position: absolute; z-index: 1; top: 100%; left: 0; right: 0; }

.header__logo { float: left; }

.header__nav { float: right; margin-right: -35px; }

/* Nav ----------------------------------- */
.nav { font-family: "OpenSans-Extrabold", Arial, sans-serif; font-size: 12px; color: #ffffff; text-transform: uppercase; }

.nav:after { content: ""; display: table; clear: both; }

.nav__item { float: left; padding: 20px 35px 20px 36px; }

.nav_header .nav__item { opacity: 0; transform: translateY(-7px); }

.nav__link { display: block; transition: all 0.5s ease-in-out; }

.nav__link:hover { color: #a70a3f; }

.nav_footer { color: #a2a4a8; }

.nav_footer .nav__item { padding-left: 24px; padding-right: 25px; }

/* =================================== Footer @mixin ================================== */
.footer { background-color: #ffffff; }

.footer__top { padding-top: 12px; padding-bottom: 12px; }

.footer__top:after { content: ""; display: table; clear: both; }

.footer__nav { float: left; margin-left: -24px; }

.footer__socials { margin-top: 15px; margin-right: -5px; float: right; }

.footer__bot { padding-top: 13px; padding-bottom: 10px; background-color: #000000; }

.footer__bot:after { content: ""; display: table; clear: both; }

.footer__copy { position: relative; top: 2px; float: left; font-size: 12px; line-height: 15px; color: rgba(255, 255, 255, 0.2); }

.footer__vintage { float: right; font-size: 0; }

