.socials__item { float: left; }

.socials__item + .socials__item { margin-left: 9px; }

.socials__link { display: block; padding: 5px; font-size: 0; color: #a2a4a8; }

.socials__item:nth-child(1) .icon { font-size: 13px; }

.socials__item:nth-child(2) .icon { font-size: 13px; }

.socials__item:nth-child(3) .icon { margin-top: 1px; font-size: 11px; }

.socials__item:nth-child(4) .icon { margin-top: 1px; font-size: 11px; }

/* =================================== Home Page @mixin ================================== */
.line-v { position: absolute; z-index: 0; pointer-events: none; }

/* Screen ----------------------------------- */
.screen { margin-top: -38px; position: relative; color: #ffffff; min-height: 670px; }

.screen__item { position: relative; height: 670px; overflow: hidden; transition: all 1s ease-in-out; }

.screen__item.slick-current { transform: scale(1.1); }

.is-loaded .screen__item.slick-current { transform: scale(1); }

.screen__slider.is-video .screen__item { pointer-events: none; }

.screen__video { pointer-events: auto; position: absolute; opacity: 0; z-index: 1; top: 50%; left: 50%; transform: scale(1.15) translate(-50%, -50%); min-width: 100%; min-height: 100%; width: auto; height: auto; transition: all 1s ease-in-out; }

.screen__video.is-active { cursor: pointer; opacity: 1; }

.is-loaded .screen__video { transform: scale(1) translate(-50%, -50%); }

.screen__img { position: absolute; z-index: 0; width: 100%; height: 100%; pointer-events: none; background-position: center center; background-repeat: no-repeat; background-size: cover; }

.screen__container { position: relative; height: 100%; padding-top: 40px; z-index: 2; font-size: 0; text-align: center; }

.screen__container:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; }

.screen__play { position: absolute; z-index: 1; left: 50%; margin-left: -486px; top: 50%; margin-top: 20px; font-family: "OpenSans-Extrabold", Arial, sans-serif; font-size: 12px; color: rgba(255, 255, 255, 0.5); line-height: 1.5; text-transform: uppercase; transition: all 0.5s ease-in-out; }

.screen__play-text { display: inline-block; vertical-align: 2px; opacity: 0; transform: translateY(-20px); }

.screen__play .icon { position: relative; top: -3px; margin-left: 20px; width: 28px; color: #ffffff; opacity: 0; transform: translateX(-10px); }

.screen__play:before { pointer-events: none; content: ''; position: absolute; z-index: 1; width: 1px; height: 0; bottom: 100%; margin-bottom: 17px; right: 15px; background-color: #ffffff; transition: all 1s ease-in-out; transition-delay: 0.5s; }

.screen__play:after { pointer-events: none; content: ''; position: absolute; z-index: 1; width: 1px; height: 0; top: 100%; margin-top: 10px; right: 15px; background-color: #ffffff; transition: all 1s ease-in-out; transition-delay: 0.5s; }

.is-loaded .screen__play:before, .is-loaded .screen__play:after { height: 34px; }

.screen__slider.is-video .screen__play { opacity: 0; transform: translateX(-50px); }

.screen__inner { margin-top: 125px; display: inline-block; vertical-align: middle; width: 100%; }

.screen__title { max-width: 460px; margin-left: auto; margin-right: auto; text-transform: uppercase; text-align: left; transition: all 0.5s ease-in-out; }

.screen__title.is-loaded { transition: all 0.5s ease-in-out; }

.screen__slider.is-video .screen__title { opacity: 0; transform: translateX(50px); }

.screen__btn { margin-top: 40px; margin-left: 300px; }

.screen__scroll { position: absolute; bottom: 25px; right: 70px; z-index: 1; font-family: "OpenSans-Extrabold", Arial, sans-serif; font-size: 12px; color: #ffffff; text-transform: uppercase; transition: all 0.5s ease-in-out; }

.screen__scroll-text { display: inline-block; vertical-align: middle; opacity: 0; transform: translateY(-20px); }

.screen__scroll-line { position: absolute; z-index: 1; height: 65px; width: 3px; right: -31px; bottom: -25px; overflow: hidden; transform: translateY(-100%); }

.screen__scroll-line:before { content: ''; position: absolute; z-index: 1; top: 0; left: 0; right: 0; bottom: 0; background-color: #ffffff; }

.screen__scroll-line.is-animated:before { animation: scroll-btn 2s infinite cubic-bezier(0.46, 0.03, 0.52, 0.96); animation-delay: -1s; }

.screen__scroll.is-video { pointer-events: none; opacity: 0; transform: translateY(50px); }

.screen__title { font-family: "OpenSans-Extrabold", Arial, sans-serif; font-size: 35px; line-height: 1.28571; }

.screen__prev { position: absolute; z-index: 3; bottom: 30px; left: calc(50% - 600px); }

.screen__next { position: absolute; z-index: 3; left: calc(50% - 310px); bottom: 30px; }

.screen__prev, .screen__next { color: rgba(255, 255, 255, 0.5); transition: all 0.5s ease-in-out; }

.screen__prev:hover, .screen__next:hover { color: #ffffff; }

.screen__dots { position: absolute; z-index: 3; bottom: 15px; left: calc(50% - 530px); font-size: 0; transition: all 0.5s ease-in-out; }

.screen__dots:after { content: ""; display: table; clear: both; }

.screen__dots-item { cursor: pointer; display: inline-block; font-family: "OpenSans-Extrabold", Arial, sans-serif; font-size: 60px; color: #ffffff; }

.screen__dots-item + .screen__dots-item { position: relative; margin-left: 55px; font-size: 24px; color: rgba(255, 255, 255, 0.5); }

.screen__dots-item + .screen__dots-item:before { content: ''; position: absolute; z-index: 1; width: 3px; height: 3px; pointer-events: none; background-color: #ffffff; top: 50%; margin-top: -2px; left: -28px; }

.screen__slider.is-video .screen__dots { pointer-events: none; opacity: 0; transform: translateY(50px); }

@keyframes scroll-btn { from { transform: translateY(-100%); }
  to { transform: translateY(100%); } }

/* Inno ----------------------------------- */
.inno { padding-top: 90px; }

.inno__top:after { content: ""; display: table; clear: both; }

.inno__top-col { float: left; }

.inno__top-col:nth-child(1) { overflow: hidden; position: relative; margin-left: -40px; width: 340px; color: #ffffff; text-transform: uppercase; }

.inno__top-col + .inno__top-col { margin-left: 70px; margin-top: 97px; max-width: 460px; }

.inno__top-title { font-family: "OpenSans-Extrabold", Arial, sans-serif; font-size: 24px; line-height: 1.25; }

