.inno__top-title .motion__inner { padding: 85px 50px 20px 40px; }

.inno__top-title .motion:before { content: ''; position: absolute; z-index: 1; top: 57px; left: 29px; width: 20px; height: 3px; background-color: #ffffff; }

.inno__top-subtitle { font-family: "OpenSans-Extrabold", Arial, sans-serif; font-size: 18px; line-height: 1.33333; text-transform: uppercase; opacity: 0; transform: translateY(50px); }

.inno__top-text { margin-top: 36px; font-size: 16px; opacity: 0; color: #808080; transform: translateY(50px); }

.inno__row { position: relative; padding-bottom: 100px; margin-left: -22px; margin-right: -23px; }

.inno__row:after { content: ""; display: table; clear: both; }

.inno__row:before { content: ""; display: table; clear: both; }

.inno__col { position: relative; margin-top: -131px; float: left; width: 33.33%; padding-left: 22px; padding-right: 23px; }

.inno__item { position: relative; padding-bottom: 85px; position: relative; z-index: 1; }

.inno__item:before { display: block; content: ""; width: 100%; padding-top: 100%; }

.inno__item-inner { overflow: hidden; position: absolute; top: 0; left: 0; right: 0; bottom: 0; z-index: 1; bottom: 85px; }

.inno__item-inner:after { content: ''; position: absolute; z-index: 2; pointer-events: none; width: 100%; height: 100%; left: 0; top: 0; background-color: #a70a3f; transform: translate(0, 0); transition: all 0.5s ease-in-out; }

.inno__item-inner.is-motion:after { transform: translate(100%, 0); }

.inno__item-in { transition: all 0.5s ease-in-out; }

.inno__item-video { position: absolute; z-index: 1; top: 0; left: 0; width: 100%; height: 100%; overflow: hidden; transition: all 0.5s ease-in-out; }

.inno__video { position: absolute; z-index: 1; top: 50%; left: 50%; transform: translate(-50%, -50%); height: 100%; }

.inno__desc { position: absolute; margin-top: -84px; top: 100%; left: 0; z-index: 2; background-color: #ffffff; margin-left: -40px; padding: 0 0 10px 0; width: calc(100% - 30px); height: 240px; transition: all 0.75s ease-in-out; }

.inno__desc-title { cursor: pointer; padding-top: 40px; margin-left: 40px; padding-right: 45px; font-family: "OpenSans-Extrabold", Arial, sans-serif; font-size: 18px; text-transform: uppercase; }

.inno__desc-title:after { content: ""; display: table; clear: both; }

.inno__desc-title-text { float: left; max-width: 200px; line-height: 1.33; }

.inno__desc-title .icon { float: right; margin-top: 4px; margin-right: 3px; }

.inno__desc-text { margin-top: 22px; padding: 0 95px 0 40px; font-size: 16px; color: #808080; opacity: 0; transition: all 0.5s ease-in-out; }

.inno__item.is-active .inno__desc { transform: translateY(-100%); opacity: 0; transition: all 0.75s ease-in-out; }

.inno__item.is-active .inno__desc-text { opacity: 1; transition: all 1.25s ease-in-out; }

.inno__item.is-active .inno__item-in { filter: grayscale(100%); }

.inno__item.is-active .inno__item-video { filter: grayscale(100%); transition: all 1.5s ease-in-out; }

/* Start ----------------------------------- */
.start { padding-top: 100px; font-size: 0; text-align: right; }

.start__item { position: relative; display: inline-block; vertical-align: top; width: 25%; text-align: left; overflow: hidden; }

.start__item:not(.start__item_title) { position: relative; }

.start__item:not(.start__item_title):before { display: block; content: ""; width: 100%; padding-top: 100%; }

.start__item:nth-child(3n + 1) .start__item-inner { color: #ffffff; background-color: #a70a3f; }

.start__item:nth-child(3n + 1) .start__item-inner:before { content: none; display: none; }

.start__item_title { display: block; float: right; height: 400px; margin-bottom: -100px; overflow: visible; }

.start__item_title .motion { padding: 0; }

.start__item_title .motion__inner { position: relative; padding: 85px 50px 20px 40px; z-index: 1; }

.start__item_title .motion:before { content: ''; position: absolute; z-index: 0; pointer-events: none; top: 0; left: 0; width: 100%; bottom: 0; background-color: #a2a4a8; transition: all 1s ease-in-out; }

.start__item_title .motion.is-motion:before { width: calc(100% + 40px); }

.start__item_title .motion:after { content: ''; position: absolute; z-index: 1; top: 55px; left: 29px; width: 20px; height: 3px; background-color: #ffffff; }

.start__item_title__inner { position: relative; z-index: 3; }

.start__item-inner { overflow: hidden; z-index: 0; background-color: #ffffff; position: absolute; top: 0; left: 0; right: 0; bottom: 0; }

.start__item-inner:after { content: ''; position: absolute; z-index: 2; pointer-events: none; width: 100%; height: 100%; left: 0; top: 0; background-color: #a70a3f; transform: translate(0, 0); transition: all 0.5s ease-in-out; }

.start__item-inner.is-motion:after { transform: translate(100%, 0); }

.start__item:nth-child(3n + 1) .start__item-inner:after, .start__item:nth-child(3n + 3) .start__item-inner:after { display: none; content: none; }

.start__item-desc { padding: 45px 0 0; z-index: 1; position: absolute; top: 0; left: 0; right: 0; bottom: 0; display: flex; flex-direction: column; }

.start__item-desc:before { content: ''; position: absolute; top: 0; left: 0; right: 0; bottom: 0; z-index: -1; background-color: #a2a4a8; transform: translateX(-100%); transition: all 0.5s ease-in-out; }

