.start__img { position: absolute; z-index: 1; top: 50%; left: 50%; transform: translate(-50%, -50%); height: 100%; }

.start__video { position: absolute; z-index: 1; top: 50%; left: 50%; transform: translate(-50%, -50%); height: 100%; }

.start__hide { flex: 1 0 auto; overflow: hidden; transform: translateX(-100%); transition: all 0.5s ease-in-out; }

.start__item:hover .start__hide { transform: translateX(0); }

.start__title { position: relative; z-index: 2; flex: 0 0 auto; padding: 0 30px; font-family: "OpenSans-Extrabold", Arial, sans-serif; font-size: 18px; text-transform: uppercase; line-height: 1.33; transition: all 0.5s ease-in-out; }

