.start__title-text { float: left; max-width: 190px; }

.start__title-icon { float: right; height: 13px; width: 3px; position: relative; top: 5px; right: 3px; transform: rotate(90deg); transition: all 0.75s ease-in-out; }

.start__title-icon:before, .start__title-icon:after { content: ''; position: absolute; display: block; right: 0; width: 3px; height: 7px; background-color: #ffffff; transition: all 0.75s ease-in-out; transform: translateZ(0); }

.start__title-icon_black:before, .start__title-icon_black:after { background-color: #000000; }

.start__title-icon:before { bottom: 50%; }

.start__title-icon:after { top: 50%; }

.start__item:hover .start__title-icon { transform: translateY(-3px) rotate(90deg); }

.start__item:hover .start__title-icon:before { transform: rotate(-40deg) translateY(2px) scaleY(1.15) translateZ(0); }

.start__item:hover .start__title-icon:after { transform: rotate(40deg) translateY(-2px) scaleY(1.15) translateZ(0); }

.start__item:hover .start__title-icon_black:before, .start__item:hover .start__title-icon_black:after { background-color: #ffffff; }

.start__item:hover .start__title { color: #ffffff; }

.start__item_title .start__title { font-size: 24px; color: #ffffff; line-height: 1.25; }

.start__text { position: relative; margin-top: 18px; padding: 0 30px; font-size: 16px; color: rgba(255, 255, 255, 0.75); top: 20px; opacity: 0; transform: translateX(100%); transition: top 0.5s ease-in-out 0.15s, transform 0.5s ease-in-out, opacity 0.5s ease-in-out; }

.start__item:hover .start__text { top: 0; opacity: 1; transform: translateX(0); transition: top 0.5s ease-in-out 0.15s, transform 0.5s ease-in-out, opacity 0.75s ease-in-out 0.15s; }

.start__btn { position: absolute; z-index: 1; bottom: 0; left: 100%; width: 100%; display: block; padding-top: 24px; padding-bottom: 23px; padding-left: 30px; font-family: "OpenSans-Extrabold", Arial, sans-serif; font-size: 12px; color: #ffffff; line-height: 12px; text-transform: uppercase; background-color: #989a9e; transform: translate(0, 100%); }

.start__btn-icon { display: inline-block; vertical-align: middle; height: 13px; width: 3px; position: relative; left: 12px; top: -1px; transition: all 0.75s ease-in-out; }

.start__btn-icon:before, .start__btn-icon:after { content: ''; position: absolute; display: block; right: 0; width: 3px; height: 7px; background-color: #ffffff; transition: all 0.75s ease-in-out; transform: translateZ(0); }

.start__btn-icon:before { bottom: 50%; }

.start__btn-icon:after { top: 50%; }

.start__btn:hover .start__btn-icon { transform: translateX(-3px); }

.start__btn:hover .start__btn-icon:before { transform: rotate(-40deg) translateY(2px) scaleY(1.15) translateZ(0); }

.start__btn:hover .start__btn-icon:after { transform: rotate(40deg) translateY(-2px) scaleY(1.15) translateZ(0); }

.start__item .start__btn { transition: transform 0.5s ease-in-out 0.5s, left 0.5s ease-in-out 0s; }

.start__item:hover .start__btn { left: 0; transform: translate(0, 0); transition: transform 0.5s ease-in-out 0.4s, left 0.5s ease-in-out 0s; }

.start__button { width: 25%; position: relative; right: -1px; margin-right: 1px; z-index: 2; }

/* Why ----------------------------------- */
.why { padding-top: 180px; }

.why__top:after { content: ""; display: table; clear: both; }

.why__top-col { float: left; }

