.why__item { position: relative; height: 100%; }

.why__item-in { overflow: hidden; position: relative; background-color: #ffffff; }

.why__item-in:after { content: ''; position: absolute; z-index: 2; pointer-events: none; width: 100%; height: 100%; left: 0; top: 0; background-color: #a70a3f; transform: translate(0, 0); transition: all 0.5s ease-in-out; }

.why__item-in.is-motion:after { transform: translate(100%, 0); }

.why__item-inner { padding: 45px 30px; }

.why__col:nth-child(2n) .why__item-in { background-color: #a2a4a8; }

.why__title { margin-top: 28px; font-family: "OpenSans-Extrabold", Arial, sans-serif; font-size: 12px; line-height: 1.5; text-transform: uppercase; }

