.why__number { font-family: "OpenSans-Extrabold", Arial, sans-serif; font-size: 60px; text-transform: uppercase; }

/* Reg ----------------------------------- */
.reg { padding-top: 80px; }

.reg__row:after { content: ""; display: table; clear: both; }

.reg__row_right { width: calc(100% + 40px); margin-right: -40px; }

.reg__col { float: left; }

.reg__col:nth-child(2) { margin-top: 65px; background-color: #a70a3f; min-height: 535px; margin-left: -30px; padding-top: 85px; padding-left: 70px; width: calc(100% - 300px); color: #ffffff; }

.reg__title { position: relative; margin-left: -40px; margin-bottom: -85px; padding: 85px 50px 20px 40px; width: 340px; height: 300px; font-family: "OpenSans-Extrabold", Arial, sans-serif; font-size: 24px; line-height: 1.25; color: #ffffff; text-transform: uppercase; background-color: #a2a4a8; }

.reg__title:before { content: ''; position: absolute; z-index: 1; top: 57px; left: 29px; width: 20px; height: 3px; background-color: #ffffff; }

