.reg__btn { margin-top: 25px; }

/* Service ----------------------------------- */
.service { padding-top: 182px; font-size: 0; text-align: right; }

.service__item { position: relative; padding: 50px 30px; display: inline-block; vertical-align: top; width: 25%; height: 300px; text-align: left; background-color: #ffffff; overflow: hidden; }

