.service__item_title:before { content: ''; position: absolute; z-index: 0; pointer-events: none; top: 0; left: 0; right: -40px; bottom: 0; background-color: #a2a4a8; }

.service__img { position: absolute; z-index: 1; top: 0; left: 0; }

.service__title { position: relative; z-index: 2; font-family: "OpenSans-Extrabold", Arial, sans-serif; font-size: 18px; text-transform: uppercase; line-height: 1.33; transition: all 0.5s ease-in-out; }

