.gal__img { position: relative; z-index: 2; width: 100%; }

.gal__item-title { position: absolute; bottom: 100%; right: 0; width: 340px; height: 350px; padding: 85px 50px 20px 40px; color: #ffffff; overflow: visible; background-color: #a2a4a8; }

