.gal__desc-text { position: relative; padding-left: 11.5%; flex-basis: 100%; font-family: "OpenSans-Extrabold", Arial, sans-serif; font-size: 18px; text-transform: uppercase; line-height: 24px; }

.gal__desc-text:before { content: '“'; position: absolute; top: -5px; left: -20px; font-size: 72px; font-family: "OpenSans-Extrabold", Arial, sans-serif; color: #ced0d6; }

