/* Map ----------------------------------- */
.map { padding-top: 30px; }

.map__list { position: relative; z-index: 2; min-height: 210px; margin-left: -22px; margin-right: -23px; color: #ffffff; overflow: hidden; margin-bottom: -140px; }

