// fonts
@include font('PTSans-Regular', 'PTSans-Regular');
@include font('OpenSans-Extrabold', 'OpenSans-Extrabold');

$default-fonts: Arial, sans-serif;
@mixin pt {
	font-family: 'PTSans-Regular', $default-fonts; }
@mixin open-extrabold {
	font-family: 'OpenSans-Extrabold', $default-fonts; }

// colors
$red: #a70a3f;
$gray-light: #a2a4a8;
$black: #000000;
$gray-dark: #808080;
$white: #ffffff;
$whitesmoke: #f5f5f5;

//title
@mixin h1-lg {
	@include open-extrabold;
	font-size: 35px;
	line-height: 1.28571; }
@mixin h1 {
	@include open-extrabold;
	font-size: 24px;
	line-height: 1.25; }
@mixin h2 {
	@include open-extrabold;
	font-size: 18px;
	line-height: 1.33333; }
@mixin h3 {
	@include open-extrabold;
	font-size: 12px;
	line-height: 1.5; }

@mixin trs {
	transition: all 0.5s ease-in-out; }

@mixin smooth {
	-webkit-font-smoothing: antialiased; }

@mixin text-slider {
	@include open-extrabold;
	font-size: 60px; }

.btn {
	display: inline-block;
	width: 300px;
	height: 60px;
	line-height: 60px;
	@include open-extrabold;
	font-size: 12px;
	color: $white;
	text-align: center;
	text-transform: uppercase;
	transition: all 0.75s ease-out;
	.btn__icon {
		display: inline-block;
		vertical-align: middle;
		height: 13px;
		width: 3px;
		position: relative;
		left: 12px;
		top: -1px;
		transition: all 0.75s ease-out;
		transform: translateZ(0);
		&:before,
		&:after {
			content: '';
			position: absolute;
			display: block;
			right: 0;
			width: 3px;
			height: 7px;
			background-color: #ffffff;
			transition: all 0.75s ease-out;
			transform: translateZ(0); }
		&:before {
			bottom: 50%; }
		&:after {
			top: 50%; }
		&_red {
			&:before,
			&:after {
				background-color: $red; } } }
	&:hover &__icon {
		&:before {
			transform: rotate(-40deg) translateY(2px) scaleY(1.15) translateZ(0); }
		&:after {
			transform: rotate(40deg) translateY(-2px) scaleY(1.15) translateZ(0); } }
	&_red {
		background-color: $red; }
	&_black {
		background-color: $black; }
	&_white {
		color: $red;
		background-color: #ffffff; } }

.arrow-bot-anim {
	display: inline-block;
	vertical-align: middle;
	height: 13px;
	width: 3px;
	position: relative;
	transform: rotate(90deg) translateZ(0);
	transition: all 0.75s ease-out;
	&:before,
	&:after {
		content: '';
		position: absolute;
		display: block;
		right: 0;
		width: 3px;
		height: 7px;
		background-color: #000;
		transition: all 0.75s ease-out;
		trnasform: translateZ(0); }
	&:before {
		bottom: 50%; }
	&:after {
		top: 50%; }
	&.is-active {
		&:before {
			transform: rotate(-40deg) translateY(2px) scaleY(1.15) translateZ(0); }
		&:after {
			transform: rotate(40deg) translateY(-2px) scaleY(1.15) translateZ(0); } } }


/* ===================================
	 Main Styles
	 @mixin ================================== */
.main {
	padding-top: 38px; }

body {
	position: relative;
	min-width: 320px;
	@include pt;
	font-size: 14px;
	color: #000;
	line-height: 1.2;
	@include smooth;
	background-color: #ffffff; }

.wrapper {
	position: relative;
	overflow: hidden;
	margin: 0 auto;
	min-height: 100%; }

.main {
	position: relative; }

.container {
	margin-left: auto;
	margin-right: auto;
	padding: 0 40px;
	max-width: 1280px; }

.preloader {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	z-index: 1000;
	background-color: #ffffff;
	transition: all 0.75s ease-in-out;
	.is-loaded & {
		opacity: 0;
		pointer-events: none; } }


/* Helpers
	----------------------------------- */
button {
	background-color: transparent; }

a:hover, a:focus {
	text-decoration: none; }

a {
	color: inherit;
	text-decoration: none; }

table {
	vertical-align: top; }

img {
	max-width: 100%;
	vertical-align: top; }

.icon {
	display: inline-block;
	vertical-align: middle; }

.text-left {
	text-align: left; }

.text-center {
	text-align: center; }

.text-right {
	text-align: right; }

.is-hidden {
	display: none; }

.vertical {
	display: inline-block;
	vertical-align: middle; }

.cfx {
	@include pie-clearfix; }

* {
	@include selection {
		color: #fff;
		background-color: $red; } }



/* ===================================
	 Header
	 @mixin ================================== */
.header {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	z-index: 100;
	background-color: lighten($red,30);
	opacity: 0;
	transform: translateY(-40px);
	transition: all 1s ease-in-out;
	.is-loaded & {
		opacity: 1;
		transform: translateY(0); }
	&__top {
		@include open-extrabold;
		color: #ffffff;
		line-height: 38px;
		background-color: #000000;
		opacity: 0;
		transform: translateY(-40px);
		transition: all 1s ease-in-out;
		@include pie-clearfix;
		.is-loaded & {
			opacity: 1;
			transform: translateY(0); } }
	&__phone {
		float: left;
		position: relative;
		padding-left: 57px;
		&:before {
			content: '';
			position: absolute;
			z-index: 1;
			top: 50%;
			left: 0;
			width: 30px;
			height: 1px;
			background-color: rgba(#ffffff, .2); } }
	&__login {
		float: right;
		font-size: 12px;
		color: rgba(#ffffff, .4);
		text-transform: uppercase;
		@include trs;
		&:hover {
			color: $red; } }
	&__lang {
		position: relative;
		position: relative;
		z-index: 101;
		float: right;
		margin-right: 30px;
		font-size: 12px;
		text-transform: uppercase;
		&-link {
			display: block;
			position: relative;
			padding-left: 10px;
			padding-right: 30px;
			color: $gray-dark;
			line-height: 30px;
			background-color: #ffffff;
			@include trs;
			&:before {
				content: '';
				position: absolute;
				z-index: 1;
				top: 50%;
				right: 9px;
				width: 0;
				height: 3px;
				margin-top: -2px;
				background-color: transparent;
				@include trs; } }
		&_parent > &-item > &-link {
			&:before,
			&:after {
				content: '';
				position: absolute;
				display: block;
				top: 50%;
				right: 9px;
				width: 7px;
				height: 3px;
				background-color: #4C4C4C;
				transition: all 0.75s ease-in-out; }
			&:after {
				right: 15px;
				margin-top: -2px; } }
		&_parent.is-active > &-item > &-link {
			&:before,
			&:after {
				background-color: #c9c9c9; }
			&:before {
				margin-top: -3px;
				transform: rotate(-40deg) translateX(-2px) scaleY(1.15); }
			&:after {
				margin-top: -3px;
				transform: rotate(40deg) translateX(2px) scaleY(1.15); } }
		&:not(&_parent) &-item:hover > &-link {
			color: $red;
			&:before {
				width: 13px;
				background-color: $red; } }
		&-item.active > &-link {
			color: #ffffff;
			line-height: 38px; }
		&-item.active > &-link {
			background-color: transparent; }
		&.is-active > &-item > &-link {
			color: $red;
			background-color: #ffffff; }
		& & {
			display: none;
			position: absolute;
			z-index: 1;
			top: 100%; } }
	&__bot {
		padding: 14px 0;
		position: absolute;
		z-index: 1;
		top: 100%;
		left: 0;
		right: 0; }
	&__logo {
		float: left; }
	&__nav {
		float: right;
		margin-right: -35px; } }

/* Nav
	----------------------------------- */
.nav {
	@include open-extrabold;
	font-size: 12px;
	color: #ffffff;
	text-transform: uppercase;
	@include pie-clearfix;
	&__item {
		float: left;
		padding: 20px 35px 20px 36px; }
	&_header &__item {
		opacity: 0;
		transform: translateY(-7px); }
	&__link {
		display: block;
		@include trs;
		&:hover {
			color: $red; } }
	&_footer {
		color: $gray-light; }
	&_footer &__item {
		padding-left: 24px;
		padding-right: 25px; } }


/* ===================================
	 Footer
	 @mixin ================================== */
.footer {
	background-color: #ffffff;
	&__top {
		padding-top: 12px;
		padding-bottom: 12px;
		@include pie-clearfix; }
	&__nav {
		float: left;
		margin-left: -24px; }
	&__socials {
		margin-top: 15px;
		margin-right: -5px;
		float: right; }
	&__bot {
		padding-top: 13px;
		padding-bottom: 10px;
		background-color: #000000;
		@include pie-clearfix; }
	&__copy {
		position: relative;
		top: 2px;
		float: left;
		font-size: 12px;
		line-height: 15px;
		color: rgba(#ffffff,.2); }
	&__vintage {
		float: right;
		font-size: 0;
		.icon {
			font-size: 15px;
			color: rgba(#ffffff, .4); } } }

/* Socials
	----------------------------------- */
.socials {
	@include pie-clearfix;
	&__item {
		float: left;
		& + & {
			margin-left: 9px; } }
	&__link {
		display: block;
		padding: 5px;
		font-size: 0;
		color: $gray-light; }
	&__item:nth-child(1) {
		.icon {
			font-size: 13px; } }
	&__item:nth-child(2) {
		.icon {
			font-size: 13px; } }
	&__item:nth-child(3) {
		.icon {
			margin-top: 1px;
			font-size: 11px; } }
	&__item:nth-child(4) {
		.icon {
			margin-top: 1px;
			font-size: 11px; } } }


/* ===================================
	 Home Page
	 @mixin ================================== */
.line-v {
	position: absolute;
	z-index: 0;
	pointer-events: none; }


/* Screen
	----------------------------------- */
.screen {
	margin-top: -38px;
	position: relative;
	color: #ffffff;
	min-height: 670px;
	&__item {
		position: relative;
		height: 670px;
		overflow: hidden;
		&.slick-current {
			transform: scale(1.1); }
		transition: all 1s ease-in-out;
		.is-loaded &.slick-current {
			transform: scale(1); } }
	&__slider.is-video &__item {
		pointer-events: none; }
	&__video {
		pointer-events: auto;
		position: absolute;
		opacity: 0;
		z-index: 1;
		top: 50%;
		left: 50%;
		transform: scale(1.15) translate(-50%, -50%);
		min-width: 100%;
		min-height: 100%;
		width: auto;
		height: auto;
		transition: all 1s ease-in-out;
		&.is-active {
			cursor: pointer;
			opacity: 1; }
		.is-loaded & {
			transform: scale(1) translate(-50%, -50%); } }
	&__img {
		position: absolute;
		z-index: 0;
		width: 100%;
		height: 100%;
		pointer-events: none;
		background-position: center center;
		background-repeat: no-repeat;
		background-size: cover; }
	&__container {
		position: relative;
		height: 100%;
		padding-top: 40px;
		z-index: 2;
		@include vertical; }
	&__play {
		position: absolute;
		z-index: 1;
		left: 50%;
		margin-left: -486px;
		top: 50%;
		margin-top: 20px;
		@include open-extrabold;
		font-size: 12px;
		color: rgba(#ffffff, .5);
		line-height: 1.5;
		text-transform: uppercase;
		@include trs;
		&-text {
			display: inline-block;
			vertical-align: 2px;
			opacity: 0;
			transform: translateY(-20px); }
		.icon {
			position: relative;
			top: -3px;
			margin-left: 20px;
			width: 28px;
			color: #ffffff;
			opacity: 0;
			transform: translateX(-10px); }
		&:before {
			pointer-events: none;
			content: '';
			position: absolute;
			z-index: 1;
			width: 1px;
			height: 0;
			bottom: 100%;
			margin-bottom: 17px;
			right: 15px;
			background-color: #ffffff;
			transition: all 1s ease-in-out;
			transition-delay: 0.5s; }
		&:after {
			pointer-events: none;
			content: '';
			position: absolute;
			z-index: 1;
			width: 1px;
			height: 0;
			top: 100%;
			margin-top: 10px;
			right: 15px;
			background-color: #ffffff;
			transition: all 1s ease-in-out;
			transition-delay: 0.5s; }
		.is-loaded &:before,
		.is-loaded &:after {
			height: 34px; } }
	&__slider.is-video &__play {
		opacity: 0;
		transform: translateX(-50px); }
	&__inner {
		margin-top: 125px;
		display: inline-block;
		vertical-align: middle;
		width: 100%; }
	&__title {
		max-width: 460px;
		margin-left: auto;
		margin-right: auto;
		text-transform: uppercase;
		text-align: left;
		@include trs;
		&.is-loaded {
			@include trs; } }
	&__slider.is-video &__title {
		opacity: 0;
		transform: translateX(50px); }
	&__btn {
		margin-top: 40px;
		margin-left: 300px; }
	&__scroll {
		position: absolute;
		bottom: 25px;
		right: 70px;
		z-index: 1;
		@include open-extrabold;
		font-size: 12px;
		color: #ffffff;
		text-transform: uppercase;
		@include trs;
		&-text {
			display: inline-block;
			vertical-align: middle;
			opacity: 0;
			transform: translateY(-20px); }
		&-line {
			position: absolute;
			z-index: 1;
			height: 65px;
			width: 3px;
			right: -31px;
			bottom: -25px;
			overflow: hidden;
			transform: translateY(-100%);
			&:before {
				content: '';
				position: absolute;
				z-index: 1;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				background-color: #ffffff; }
			&.is-animated {
				&:before {
					animation: scroll-btn 2s infinite cubic-bezier(0.46, 0.03, 0.52, 0.96);
					animation-delay: -1s; } } }
		&.is-video {
			pointer-events: none;
			opacity: 0;
			transform: translateY(50px); } }
	&__title {
		@include h1-lg; }
	&__prev {
		position: absolute;
		z-index: 3;
		bottom: 30px;
		left: calc(50% - 600px); }
	&__next {
		position: absolute;
		z-index: 3;
		left: calc(50% - 310px);
		bottom: 30px; }
	&__prev,
	&__next {
		color: rgba(#ffffff, .5);
		@include trs;
		&:hover {
			color: #ffffff; } }
	&__dots {
		position: absolute;
		z-index: 3;
		bottom: 15px;
		left: calc(50% - 530px);
		font-size: 0;
		@include trs;
		@include pie-clearfix;
		&-item {
			cursor: pointer;
			display: inline-block;
			@include open-extrabold;
			font-size: 60px;
			color: #ffffff;
			& + & {
				position: relative;
				margin-left: 55px;
				font-size: 24px;
				color: rgba(#ffffff, .5);
				&:before {
					content: '';
					position: absolute;
					z-index: 1;
					width: 3px;
					height: 3px;
					pointer-events: none;
					background-color: #ffffff;
					top: 50%;
					margin-top: -2px;
					left: -28px; } } } }
	&__slider.is-video &__dots {
		pointer-events: none;
		opacity: 0;
		transform: translateY(50px); } }

@keyframes scroll-btn {
	from {
		transform: translateY(-100%); }
	to {
		transform: translateY(100%); } }

/* Inno
	----------------------------------- */
.inno {
	padding-top: 90px;
	&__top {
		@include pie-clearfix;
		&-col {
			float: left;
			&:nth-child(1) {
				overflow: hidden;
				position: relative;
				margin-left: -40px;
				width: 340px;
				color: #ffffff;
				text-transform: uppercase; }
			& + & {
				margin-left: 70px;
				margin-top: 97px;
				max-width: 460px; } }
		&-title {
			@include h1;
			.motion {
				position: relative;
				z-index: 1;
				top: 0;
				left: 0;
				width: 100%;
				height: 423px;
				background-color: $gray-light;
				&__inner {
					padding: 85px 50px 20px 40px; }
				&:before {
					content: '';
					position: absolute;
					z-index: 1;
					top: 57px;
					left: 29px;
					width: 20px;
					height: 3px;
					background-color: #ffffff; } } }
		&-subtitle {
			@include h2;
			text-transform: uppercase;
			opacity: 0;
			transform: translateY(50px); }
		&-text {
			margin-top: 36px;
			font-size: 16px;
			opacity: 0;
			color: $gray-dark;
			transform: translateY(50px); } }
	&__row {
		position: relative;
		padding-bottom: 100px;
		margin-left: -22px;
		margin-right: -23px;
		@include pie-clearfix;
		&:before {
			content: "";
			display: table;
			clear: both; } }
	&__col {
		position: relative;
		margin-top: -131px;
		float: left;
		width: 33.33%;
		padding-left: 22px;
		padding-right: 23px; }
	&__item {
		position: relative;
		padding-bottom: 85px;
		@include ratio(1,1);
		z-index: 1;
		&-inner {
			overflow: hidden;
			@include ratio-in;
			z-index: 1;
			bottom: 85px;
			&:after {
				content: '';
				position: absolute;
				z-index: 2;
				pointer-events: none;
				width: 100%;
				height: 100%;
				left: 0;
				top: 0;
				background-color: $red;
				transform: translate(0,0);
				@include trs; }
			&.is-motion {
				&:after {
					transform: translate(100%,0); } } }
		&-in {
			@include trs; }
		&-video {
			position: absolute;
			z-index: 1;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			overflow: hidden;
			@include trs; } }
	&__video {
		position: absolute;
		z-index: 1;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		height: 100%; }
	&__desc {
		position: absolute;
		margin-top: -84px;
		top: 100%;
		left: 0;
		z-index: 2;
		background-color: #ffffff;
		margin-left: -40px;
		padding: 0 0 10px 0;
		width: calc(100% - 30px);
		height: 240px;
		transition: all 0.75s ease-in-out;
		&-title {
			cursor: pointer;
			padding-top: 40px;
			margin-left: 40px;
			padding-right: 45px;
			@include open-extrabold;
			font-size: 18px;
			text-transform: uppercase;
			@include pie-clearfix;
			&-text {
				float: left;
				max-width: 200px;
				line-height: 1.33; }
			.icon {
				float: right;
				margin-top: 4px;
				margin-right: 3px; } }
		&-text {
			margin-top: 22px;
			padding: 0 95px 0 40px;
			font-size: 16px;
			color: $gray-dark;
			opacity: 0;
			transition: all 0.5s ease-in-out; } }
	&__item.is-active &__desc {
		transform: translateY(-100%);
		opacity: 0;
		transition: all 0.75s ease-in-out; }
	&__item.is-active &__desc-text {
		opacity: 1;
		transition: all 1.25s ease-in-out; }
	&__item.is-active &__item-in {
		filter: grayscale(100%); }
	&__item.is-active &__item-video {
		filter: grayscale(100%);
		transition: all 1.5s ease-in-out; } }

/* Start
	----------------------------------- */
.start {
	padding-top: 100px;
	font-size: 0;
	text-align: right;
	&__item {
		position: relative;
		display: inline-block;
		vertical-align: top;
		width: 25%;
		text-align: left;
		overflow: hidden;
		&:not(&_title) {
			@include ratio(1,1); }
		&:nth-child(3n + 1) &-inner {
			color: #ffffff;
			background-color: $red;
			&:before {
				content: none;
				display: none; } }
		&_title {
			display: block;
			float: right;
			height: 400px;
			margin-bottom: -100px;
			overflow: visible;
			.motion {
				padding: 0;
				&__inner {
					position: relative;
					padding: 85px 50px 20px 40px;
					z-index: 1; }
				&:before {
					content: '';
